/* settings global */
.colorButton{
    background: rgb(0, 200, 62);
}

.contenidoCentrado{
    text-decoration: none;
    display: flex;
    flex-direction: column;
}

/* settings general */
.logo{
    width: 400px;
    padding-bottom: 40px;
}

.logoNav{
    height:30px;
}

body{
    background: #eeeeee;
}

section{
    width: 100%;
    height: 100vh;
}

.navgeneral{
    background: #00a616;
    position: fixed;
    width: 100%;
    z-index: 1;
}

.contenidoLogin{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 16px;
}

.contenidoRegister{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-top: 50px;
}

.contenidoPrincipal{
    width: 100%;
    height: 100vh;
    padding-top: 5%;
}

.subcontenidoPrincipal{
    display: grid;
    grid-template-columns: repeat(3,1fr) ;
    grid-auto-rows: 550px;
    gap: 1.5em;
}

.subcontenidoPrincipal img{
    height: 370px;
}

@media screen and (max-width:400px) {
    .logo{
        width: 300px;
    }

    .contenidoRegister{
        padding-top: 40%;
    }

    .contenidoPrincipal{
        display: flex;
        justify-content: center;
        padding-top: 20%;
    }

    .subcontenidoPrincipal{
        grid-template-columns: repeat(1,1fr);
    }

    .card{
        width: 400px;
    }

    .subcontenidoPrincipal img{
        height: 300px;
    }
}